





















































export default {
	data() {
		return {
			credit_card: {
				credit_id: '',
				line_credit: '',
				credit_code: '',
				bank_name: '',
				bill_time:'',
				repay_time:'',
			},
			loading:false
		}
	},
	onLoad(e) {
		if (e.credit_id) {
			this.credit_card.credit_id = e.credit_id;
		} else {
			uni.switchTab({
				url: '/pages/tabBar/card/card'
			})
		}
	},
	onShow() {this.getCardInfo();},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	computed: {},
	methods: {
		getCardInfo(){
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const credit_id = this.credit_card.credit_id;
			try {
				this.$http.post(this.websiteUrl + '/api/card/getCreditCardInfo', {
					openid,
					sessionKey,
					credit_id
				}, (res) => {
					if (res.data.code == 1) {
						this.credit_card = res.data.result;
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				})
			}
			
		},
		editCredit(){
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const credit_id = this.credit_card.credit_id;
			const bill_time =this.credit_card.bill_time;
			const line_credit =this.credit_card.line_credit;
			const repay_time =this.credit_card.repay_time;
			
			const that = this;
			
			if(line_credit==''){
				uni.showModal({
					content: '请输入信用卡额度',
					showCancel: false
				});
				return;
			}
			
			try {
				if(this.loading){
					return;
				}
				this.loading = true;
				
				this.$http.post(this.websiteUrl + '/api/card/editCredit', {
					openid,
					sessionKey,
					credit_id,
					line_credit,
					bill_time,
					repay_time
				}, (res) => {
					if(res.data.code==1){
						uni.showModal({
							content: res.data.msg,
							showCancel:false,
							success:function() {
								that.loading = false;
								uni.navigateBack();
							}
						})
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false,
							success:function() {
								that.loading = false;
							}
						})
					}
					
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false,
					success:function() {
						that.loading = false;
					}
				});
			}
		},

	},
	onBackPress() {
		
	},
	onUnload() {
		
	}
}
